<template>
  <!--eslint-disable -->
  <div class="Robbing-content">
    <V-Scroll ref="robbingScroll" :scroll-height="`${mainHeight}px`" :pulldown="activeLi" :autoLoad="false" :on-scroll-bottom="activeLi">
      <div class="topList">
        <V-Scroll
          ref="timeScroll"
          class="filters-scroll"
          scroll-height="1.65rem"
          :hideXBar="true"
          scroll-direction="X"
          :isList="false"
        >
          <ul>
            <li :class="{'clickStyle':currentTime === item}" v-for="(item, index) in timeList" :key="index" @click="handleChangeTime(item)">
              <p>{{changeTime(item)}}</p>
              <p>{{timeListStatus[index]}}</p>
              <div class="border">
              </div>
              <span></span>
            </li>
          </ul>
        </V-Scroll>
      </div>
      <div class="centerList">
        <ul>
          <router-link
            tag="li"
            v-for="(item, index) in robbingList"
            :key="index"
            :to="`/goods/details/${item.id}`"
          >
          <img :src="item.cover" alt=""/>
          <div class="right">
            <p>{{item.name | filterSubStrName(18)}}</p>
            <!--<p class="right_border">立省398元</p>-->
            <div class="rightNum">
              <p><span></span>{{item.integral}}</p>
              <p>0元抢兑</p>
            </div>
          </div>
          </router-link>
        </ul>
      </div>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";

// 抢兑时间列表
const robbingTimeUrl = "/goods/get/rob/time/list";
// 抢兑时间列表
const robbingUrl = "/goods/get/rob/list";

/* eslint-disable */
export default {
  name: "Robbing",
  mixins: [Ajax],
  data() {
    return {
      // 抢兑时间列表
      timeList: [],
      windWidth: window.innerHeight,
      robbingList: [],
      goodsId: 0,
      timeDay: "",
      indexs: 0,
      timeListStatus: [],
      isPromise: false,
      currentTime: ""
    };
  },
  methods: {
    goDetails(id) {
      this.$router.push({ path: `/goods/details/${id}` });
    },
    handleChangeTime(time) {
      this.currentTime = time;
      const promise = this.activeLi(0);
      if (promise) {
        promise.then(this.handleDataTip);
      }
    },
    changeTime(time) {
      var newTime = parseInt(time.substring(8, 10)),
        houseTime = time.substring(11, 16);
      return newTime + "日" + houseTime;
    },
    upDate() {
      return this.post(robbingTimeUrl).then(body => {
        var timestamp = Date.parse(new Date());
        for (var i = 0; i < body.length; i++) {
          if (timestamp < Date.parse(new Date(body[i]))) {
            this.timeListStatus.push("未开始");
          } else if (
            timestamp <
            Date.parse(new Date(body[i])) + 1000 * 60 * 60
          ) {
            this.timeListStatus.push("已开抢");
          } else {
            this.timeListStatus.push("已结束");
          }
        }
        this.timeList = body;
        // 保存当前选中时间
        this.currentTime = body[0];
        if (body.length > 0) {
          const promise = this.activeLi(0, 0, body[0]);
          console.log(promise)
          if (promise) {
            promise.then(this.handleDataTip);
          }
          this.$nextTick(() => {
            this.$refs.timeScroll.refresh();
          });
        }
      });
    },
    activeLi(page) {
      if (!this.isPromise) return;
      page === 0 && (this.robbingList = []);
      return this.post(robbingUrl, {
        saleType: 5,
        robType: 0,
        startPage: page,
        time: this.currentTime
      }).then(body => {
        this.robbingList = (this.robbingList || []).concat(body.items);
        if (body.length <= 0) {
          return false;
        }
        return body;
      });
    },
    handleDataTip(result) {
      if (typeof result === "boolean") {
        // 如果列表没有数据，则更新提示状态
        this.$refs.robbingScroll.notData();
      } else {
        if (result.hasNextPage) {
          // 如果有数据则更改状态并把页码设置为下一页：1
          this.$refs.robbingScroll.success(1);
        } else {
          this.$refs.robbingScroll.notData("没有更多数据了");
        }
      }
      this.$nextTick(() => {
        this.$refs.robbingScroll.refresh();
      });
    }
  },
  mounted() {
    // 获取本地当前时间
    var myDate = new Date();
    console.log(myDate);
    // 抢兑时间列表
    this.isPromise = this.upDate();
  }
};
</script>

<style scoped>
.Robbing-content {
  background: #f1f1f1;
}

.topList ul {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  white-space: nowrap;
  color: #888;
}

.topList ul li {
  display: inline-block;
  padding: 0.27rem 0.29rem;
  white-space: nowrap;
  font-weight: normal;
  font-size: 0.36rem;
  position: relative;
  height: 0.91rem;
}

.topList ul li p {
  text-align: center;
}

.border {
  border-right: solid 1px #dddddd;
  height: 0.85rem;
  position: absolute;
  right: 0;
  top: 0.4rem;
}

.clickStyle {
  background-color: #de2245;
  color: #fff;
}

.clickStyle .border {
  border-right: solid 1px #de2245;
}

.clickStyle span {
  width: 0.14rem;
  height: 0.07rem;
  display: inline-block;
  background: url("../../images/xiangqing/bottom_jiao.png") no-repeat;
  background-size: 100%;
  position: absolute;
  bottom: -0.05rem;
  left: 50%;
}

.centerList ul {
  clear: both;
  padding-bottom: 0.45rem;
}

.centerList ul li {
  background: #fff;
  padding: 0.45rem;
  height: 3rem;
  margin-top: 0.3rem;
}

.centerList ul li img {
  width: 3rem;
  height: 3rem;
  float: left;
  margin-right: 0.45rem;
}

.right {
  float: left;
  width: 6.5rem;
}

.right p:nth-child(1) {
  font-family: PingFang-SC-Medium;
  font-size: 0.45rem;
  /*overflow: hidden;*/
  /*text-overflow: ellipsis;*/
  /*display: -webkit-box;*/
  /*-webkit-box-orient: vertical;*/
  /*-webkit-line-clamp: 1;*/
}

.right_border {
  width: 2.17rem;
  height: 0.72rem;
  background-color: #f8f8f8;
  border-radius: 0.36rem;
  border: solid 1px #de2245;
  font-size: 0.36rem;
  line-height: 0.72rem;
  text-align: center;
  margin-top: 0.3rem;
  color: #de2245;
}

.rightNum {
  margin-top: 1.5rem;
}

.rightNum p {
  float: left;
}

.rightNum p:nth-child(1) {
  color: #de2245;
  font-size: 0.81rem;
}

.rightNum p:nth-child(1) span {
  width: 0.69rem;
  height: 0.63rem;
  display: inline-block;
  background: url("../../images/home/currency.png") no-repeat;
  background-size: 100%;
}

.rightNum p:nth-child(2) {
  float: right;
  width: 3rem;
  height: 0.96rem;
  background-color: #de2245;
  border-radius: 0.47rem;
  line-height: 0.86rem;
  text-align: center;
  color: #ffffff;
  font-family: PingFang-SC-Medium;
}
</style>



